.mui-product-wrap{box-sizing:border-box;-webkit-align-items:flex-start;-ms-flex-align:start;-ms-grid-row-align:flex-start;-webkit-box-align:start;align-items:flex-start;padding:15px;font-size:14px;background-color:#fafafa;position:relative;overflow-x:hidden}.mui-product-wrap:before{content:" ";position:absolute;left:0;top:0;width:100%;height:1px;border-top:1px dashed #e0e0e0;color:#e0e0e0;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(0.5);transform:scaleY(0.5);left:15px}.mui-product-wrap.unavaliable{background-color:#f4f4f4}.mui-product-wrap.unavaliable:before{border-top:1px solid #e0e0e0;color:#e0e0e0;left:0px}.mui-product-wrap:nth-child(2):before{border-top:1px solid #e0e0e0;color:#e0e0e0;left:0px}.mui-product-wrap .left-wrap{-webkit-flex-grow:250;-ms-flex-positive:250;-webkit-box-flex:250;flex-grow:250;-webkit-align-self:center;-ms-flex-item-align:center;align-self:center}.mui-product-wrap .middle-wrap{-webkit-flex-grow:650;-ms-flex-positive:650;-webkit-box-flex:650;flex-grow:650}.mui-product-wrap .right-wrap{-webkit-flex-grow:250;-ms-flex-positive:250;-webkit-box-flex:250;flex-grow:250;text-align:right}.mui-product-wrap .right-wrap .bottom-label{position:absolute;bottom:15px;right:10px}.mui-product-wrap .product-img{width:100%}.mui-product-wrap .product-title{font-weight:normal;font-size:14px;margin-bottom:8px}.mui-product-wrap .product-detail{margin-bottom:12px}



