.mui_mask{position:fixed;z-index:1;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.6)}.mui_dialog{position:fixed;z-index:13;width:85%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:#FAFAFC;text-align:center;border-radius:3px}.mui_dialog_confirm .mui_dialog .mui_dialog_hd{padding:1.2em 20px .5em}.mui_dialog_confirm .mui_dialog .mui_dialog_bd{text-align:left}.mui_dialog_hd{padding:1.2em 0 .5em}.mui_dialog_title{font-weight:400;font-size:17px}.mui_dialog_bd{padding:0 20px;font-size:15px;color:#888;text-align:left}.mui_dialog_ft{position:relative;line-height:42px;margin-top:20px;font-size:17px;display:-webkit-flex;display:-ms-flexbox;display:-webkit-box;display:flex}.mui_dialog_ft a{display:block;-webkit-flex:1;-ms-flex:1;-webkit-box-flex:1;flex:1;color:#3CC51F;text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}.mui_dialog_ft a:active{background-color:#EEEEEE}.mui_dialog_ft:after{content:" ";position:absolute;left:0;top:0;width:100%;height:1px;border-top:1px solid #D5D5D6;color:#D5D5D6;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(.5);transform:scaleY(.5)}.mui_dialog_confirm .mui_dialog_ft a{position:relative}.mui_dialog_confirm .mui_dialog_ft a:after{content:" ";position:absolute;left:0;top:0;width:1px;height:100%;border-left:1px solid #D5D5D6;color:#D5D5D6;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleX(.5);transform:scaleX(.5)}.mui_dialog_confirm .mui_dialog_ft a:first-child:after{display:none}.mui_dialog_general .mui_dialog_ft{display:block}.mui_dialog_general .mui_dialog_ft a{position:relative;display:block}.mui_dialog_general .mui_dialog_ft a:before{content:" ";position:absolute;left:0;top:0;width:100%;height:1px;border-top:1px solid #D5D5D6;color:#D5D5D6;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(.5);transform:scaleY(.5)}.mui_dialog_general .mui_dialog_ft a:first-child:before{display:none}.mui_btn_dialog.default{color:#353535}.mui_btn_dialog.primary{color:#0BB20C}@media screen and (min-width:1024px){.mui_dialog{width:35%}}


