.mui_btn{position:relative;display:block;margin-left:auto;margin-right:auto;padding-left:14px;padding-right:14px;box-sizing:border-box;font-size:18px;text-align:center;text-decoration:none;color:#FFFFFF;line-height:2.33333333;border-radius:5px;-webkit-tap-highlight-color:rgba(0,0,0,0);overflow:hidden}.mui_btn:after{content:" ";width:200%;height:200%;position:absolute;top:0;left:0;border:1px solid rgba(0,0,0,0.2);-webkit-transform:scale(.5);transform:scale(.5);-webkit-transform-origin:0 0;transform-origin:0 0;box-sizing:border-box;border-radius:10px}.mui_btn+.mui_btn{margin-top:15px}.mui_btn.mui_btn_inline{display:inline-block}.mui_btn.mui_btn_mini{line-height:1.9;font-size:14px;padding:0 .75em;display:inline-block}.mui_btn_default{background-color:#F7F7F7;color:#454545}.mui_btn_default:not(.mui_btn_disabled):visited{color:#454545}.mui_btn_default:not(.mui_btn_disabled):active{color:#A1A1A1;background-color:#DEDEDE}.mui_btn_brand{background-color:#0093D5}.mui_btn_brand:not(.mui_btn_disabled):visited{color:#FFFFFF}.mui_btn_brand:not(.mui_btn_disabled):active{color:rgba(255,255,255,0.4);background-color:#268CBC}.mui_btn_primary{background-color:#04BE02}.mui_btn_primary:not(.mui_btn_disabled):visited{color:#FFFFFF}.mui_btn_primary:not(.mui_btn_disabled):active{color:rgba(255,255,255,0.4);background-color:#039702}.mui_btn_warn{background-color:#EF4F4F}.mui_btn_warn:not(.mui_btn_disabled):visited{color:#FFFFFF}.mui_btn_warn:not(.mui_btn_disabled):active{color:rgba(255,255,255,0.4);background-color:#C13E3E}.mui_btn_disabled{color:rgba(255,255,255,0.6)}.mui_btn_disabled.mui_btn_default{color:#C9C9C9}


